<template>
    <div class="page">
        <!-- header-section-->
        <div :class="{'header-section':true, scroll:isScroll}">
            <div class="header-classifyIcon" @click="$router.push('/goods/classify')"></div>
            <div class="header-search" @click="$router.push('goods/search')">
                <div class="search-icon"></div>
                <div class="text">请输入商品名称</div>
            </div>
            <div class="header-login"  v-if="!isLogin" @click="$router.push('/login')">登录</div>
            <div data-v-76d17a6e="" v-else class="my-icon" @click="$router.push('/pcenter')"></div>
        </div>
        <!-- banner-section-->
        <div class="banner-section">
            <div class="swiper-container">
                <div class="swiper-wrapper">
                    <div class="swiper-slide" v-for="(item,index) in SwiperData" :key="index">
                        <img :src="item.image" :alt="item.title">
                    </div>

                </div>
                <div class="swiper-pagination"></div>
            </div>

        </div>
        <!-- quick-section-->
        <div class="quick-section">
            <ul class="quick-item" @click="$router.push('/goods/classify/item?cid='+item.cid)" v-for="(item,index) in quickData" :key="index" >
                <li>
                    <img :data-echo="item.image" src="../../assets/images/common/lazyImg.jpg" :alt="item.title">
                </li>
                <li>{{item.title}}</li>
            </ul>

        </div>
        <!-- goods-section-->
        <template v-for="(item,index) in goodsData">
            <div class="goods-section" :key="index" v-if="(index+1)%2!==0">
                <div :class="'goods-title color-'+index">—— {{item.title}} ——</div>
                <div class="goods-row-1">
                    <div class="goods-column" @click="$router.push('/goods/details?gid='+(item.items && item.items[0].gid))">
                        <div class="column-title">{{item.items && item.items[0].title}}</div>
                        <div class="column-tip">精品打折</div>
                        <div :class="'column-price bgColor-'+index">{{item.items && item.items[0].price}}元</div>
                        <div class="column-image">
                            <img :data-echo="item.items && item.items[0].image" src="../../assets/images/common/lazyImg.jpg" :alt="item.items && item.items[0].title">
                        </div>
                    </div>
                    <div class="goods-column">
                        <div class="column-list"
                             @click="$router.push('/goods/details?gid='+(item2.gid))"
                             v-for="(item2,index2) in item.items.slice(0,2)" :key="index2">
                            <div class="column-list-title">{{item2.title}}</div>
                            <div class="column-list-tip">品质精挑</div>
                            <div class="column-list-image">
                                <img :data-echo="item2.image" src="../../assets/images/common/lazyImg.jpg" :alt="item2.title">
                            </div>
                        </div>

                    </div>

                </div>
                <div class="goods-row-2">
                    <div class="goods-list"
                         @click="$router.push('/goods/details?gid='+(item2.gid))"
                         v-for="(item2,index2) in item.items.slice(2,6)" :key="index2">
                        <div class="goods-list-title">{{item2.title}}</div>
                        <div class="goods-list-image">
                            <img :data-echo="item2.image" src="../../assets/images/common/lazyImg.jpg" :alt="item2.title">
                        </div>
                        <div class="goods-list-price">¥{{item2.price}}</div>
                        <div class="goods-list-price line">¥{{item2.price*2}}</div>
                    </div>

                </div>
            </div>
            <div class="goods-section" v-else>
                <div :class="'goods-title color-'+index">—— {{item.title}} ——</div>
                <div class="goods-row-1">
                    <div class="goods-column"
                         @click="$router.push('/goods/details?gid='+(item2.gid))"
                         v-for="(item2,index2) in item.items.slice(0,2)" :key="index2">
