                         @click="$router.push('/goods/details?gid='+(item3.gid))"
                         v-for="(item3,index3) in item.items.slice(2,6)" :key="index3">
                        <div class="goods-list-title">{{item3.title}}</div>
                        <div class="goods-list-image">
                            <img :data-echo="item3.image" src="../../assets/images/common/lazyImg.jpg" :alt="item3.title">
                        </div>
                        <div class="goods-list-price">¥{{item3.price}}</div>
                        <div class="goods-list-price line">¥{{item3.price*2}}</div>
