                <div class="recommend-list-price">¥{{item.price}}</div>
            </div>
        </div>
    </div>
</template>

<script>
    import Swiper from "swiper"
    import {mapActions, mapState} from "vuex"

    export default {
        data() {
            return {
                //滑动样式变量
                isScroll: false
            }
        },
        computed: {
            ...mapState({
                //轮播图数据
                SwiperData: (state) => state.index.SwiperData,
                //首页导航数据
                quickData: (state) => state.index.quickData,
                //首页产品核心数据
                goodsData: (state) => state.index.goodsData,
                //首页推荐产品数据
                recommendData:(state) =>state.index.recommendData,
                isLogin:(state)=>state.user.isLogin,
            })
        },

        created() {

            //运行getSwiper数据
            this.getSwiper({
                success: () => {
                    this.$nextTick(() => {
                        new Swiper('.swiper-container', {
                            autoplay: 3000,//可选选项，自动滑动
                            pagination: '.swiper-pagination',//分页器
                            paginationClickable: true,//点击分页器
                            autoplayDisableOnInteraction: false//播放
                        })
                    })
                }
            });
            //运行首页导航方法
            this.getQuick({success:()=>{
                this.$nextTick(()=>{
                    this.$utils.lazyImg()
                })
                }});
            //运行首页产品核心数据方法
            this.getGoods({success:()=>{
                    this.$nextTick(()=>{
                        this.$utils.lazyImg()
                    })
                }});
            //运行首页推荐数据方法
            this.getRecommend({success:()=>{
                    this.$nextTick(()=>{
                        this.$utils.lazyImg()
                    })
                }});
            //优化性能 解决滑动执行多次
            this.Scroll = true;
            //监听滑动事件 改变样式
            window.addEventListener("scroll", this.eventSrcollTop)
        },
        mounted() {

        },
        methods: {

            //vuex的异步actions
            ...mapActions({
                //轮播图方法
                getSwiper: "index/getSwiper",
                //首页导航方法
                getQuick: "index/getQuick",
                //首页产品核心方法
                getGoods: "index/getGoods",
                //首页推荐数据
                getRecommend:"index/getRecommend"
            }),
            //当滑动到150时候 header-section样式改变
            eventSrcollTop() {
                let srcollStyleTop = document.body.scrollTop || document.documentElement.scrollTop;
                if (srcollStyleTop >= 150) {
                    if (this.Scroll) {
                        this.Scroll = false;
                        this.isScroll = true;
                    }

                } else {
                    if (!this.Scroll) {
                        this.Scroll = true;
                        this.isScroll = false;
                    }

                }
            }
        },

        beforeDestroy(){

        },
        //路由离开页面时候
        destroyed() {

            this.isScroll =false;
            this.Scroll = false;
            window.removeEventListener("scroll", this.eventSrcollTop);

        },
        //keep-alive进入时触发
        activated() {
            this.Scroll = true;
            window.addEventListener("scroll", this.eventSrcollTop)
        },
        //keep-alive离开时触发
        deactivated() {
            this.isScroll =false;
            this.Scroll = false;
            window.removeEventListener("scroll", this.eventSrcollTop);
        }

    }
</script>

